// COLOR

// fffcf2-ccc5b9-403d39-252422-eb5e28 WHITE
$color-background: #fffcf2;
$color-main: #403d39;
$color-special: #eb5e28;

// d8dbe2-a9bcd0-58a4b0-373f51-1b1b1e WHITE
$color-background: #d8dbe2;
$color-background-2: #a9bcd0;
$color-main: #1b1b1e;
$color-main-2: #373f51;
$color-special: #58a4b0;

// d8dbe2-a9bcd0-58a4b0-373f51-1b1b1e BLACK
// $color-background: #1b1b1e
// $color-main: #d8dbe2
// $color-special: #58a4b0


// animation
$animation-time: 0.4s;

// BASE SIZE
$font-base-size: 6px;
$eng-scale: 0.9;

// FONT SIZE
$font-big: $font-base-size*5;
$font-medium: $font-base-size*3;
$font-small: $font-base-size*3;
$font-content: $font-base-size*2;


@mixin center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%); } //改為對齊該元素的中心

@mixin size($w,$h) {
  width: $w;
  height: $h; }

@mixin RWD-font-size($org) {
  @media all and (min-width: 600px) {
    font-size: $org; }
  @media all and (max-width: 600px) {
    font-size: $org*0.8; } }

@mixin text($size,$spacing,$color) {
  font-size: $size;
  letter-spacing: $spacing;
  color: $color; }

@keyframes fadein {
  from {
    transform: translateY(30%);
    opacity: 0; }
  to {
    transform: translateY(0%);
    opacity: 1; } }

@import url('https://fonts.googleapis.com/css?family=Noto+Serif+TC:400,600&subset=chinese-traditional');


* {
  // border: solid 1px
  font-weight: 500;
  position: relative;
  user-select: none; }




html,body {
  background-color: $color-background;
  margin: 0px;
  padding: 0px; }

.content {
  @include text($font-content,0.2em,$color-main);

  @media all and (min-width: 600px) {
    padding: 4rem;
    padding-top: 9em; }
  @media all and (max-width: 600px) {
    padding: 2rem;
    padding-top: 9em; }

  margin-top: 0; }

::-webkit-scrollbar {
  width: 0px; }

