@import "Base";

.content {

  .md {
    @media all and (min-width: 600px) {
      width: 65%; }
    @media all and (max-width: 600px) {
      width: 100%; }
    // max-width: 75%
    // margin: 0 auto
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include RWD-font-size(1.2em);
    margin-left: auto;
    margin-right: auto;
    * {
      letter-spacing: 0.06em;
      user-select: text; }

    p {

      // font-family: "Times New Roman", Times, serif
 }      // display: flex
    img {
      display: block;
      margin-left: auto;
      margin-right: auto;

      max-height: 35rem;
      @media all and (min-width: 600px) {
        max-width: 50%; }
      @media all and (max-width: 600px) {
        max-width: 85%; } }

    .my_hljs {
      padding: 1.5em;
      border-radius: 0.5em;
      width: 85%;
      margin-left: auto;
      margin-right: auto; }

    table {
      margin-top: 2em;
      border-collapse: collapse;
      width: 85%;
      margin-left: auto;
      margin-right: auto;

      th {
        color: $color-background;
        background-color: $color-main;
        text-align: left; }

      th, td {
        padding: 1em;
        border-bottom: 1px solid $color-main; } }

    a:link, a:visited {
      background-color: $color-main-2;
      color: $color-background;
      padding: 0.2rem 0.4rem;
      text-decoration: none;
      border-radius: 0.3rem; }


    a:hover, a:active {
      background-color: $color-special; }

    blockquote {
      font-size: $font-small*0.8;
      background-color: $color-background-2;
      // font-style: italic
      border-left: 0.3em solid $color-special;

      padding:  1em 0em 0em 2em;
      padding-bottom: 0;
      margin: 0;
      display: flex;
      flex-direction: column;
      align-item: flex-end;
 } } }      // tr:nth-child(even)
