

@import "Base";

$header-height: 6em;
$header-trans: 0.4s;

.header.nav-down {
  transition: $header-trans;
  top: -($header-height + 1em); }

.header.nav-up {
  transition: $header-trans;
  top: 0em; }

.header {
  z-index: 10;
  display: flex;
  position: fixed;
  height: $header-height;
  right: 0;
  width: 100%;
  // padding: 10px
  justify-content: flex-end;
  // BLACK
  background-color: $color-background;
  border-bottom: solid 0.1em $color-main;
  // WHITE
  background-color: $color-main;
  @media all and (min-width: 600px) {
    flex-direction: row; }
  @media all and (max-width: 600px) {
    flex-direction: column; }



  .button_small_broad_pad {
    display: flex;
    flex-grow: 9;
    @media all and (min-width: 600px) {
      padding: 0em 4rem; }
    @media all and (max-width: 600px) {
      padding: 0em 2rem;
      justify-content: center; }
    img {
      @media all and (min-width: 600px) {
        margin: auto 0em;
        height: 65%; }
      @media all and (max-width: 600px) {
        margin: 1em;
        height: 2rem; } } }

  .button_small_broad {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    // background-color: $color-special
    @media all and (min-width: 600px) {
      padding: 0em 4rem; }
    @media all and (max-width: 600px) {
      padding: 0em 2rem; }


    .button_small {
      margin: 0.5em 0em;
      align-self: flex-end;
      cursor: pointer;
      padding: 0.5em 0em;
      & a {
        text-decoration: none;
        // BLACK
        @include text($font-content,0.2em,$color-main);
        // WHITE
        @include text($font-content,0.2em,$color-background);
        @include RWD-font-size($font-small*$eng_scale);
        transition-duration: $animation-time; }
      & :hover {
          color: $color-special; } } } }



