
@import "Base";

.content {

  h5 {
    margin-top: 1em; }

  h1 {
    @include RWD-font-size($font-big);
    font-weight: 700; }
  h1.eng {
    @include RWD-font-size($font-big*$eng-scale); }

  h2 {
    @include RWD-font-size($font-medium);
    font-weight: 700;
    letter-spacing: 0.2em; }
  h2.eng {
    @include RWD-font-size($font-medium*$eng-scale); }


  h3 {
    @include RWD-font-size($font-small); }
  h3.eng {
    @include RWD-font-size($font-small*$eng-scale); }

  .eng {
    margin-top: -15px;
    font-family: geogia; }

  .button {
      animation: fadein 1s;
      transition-duration: $animation-time;
      padding: 5px;
      cursor: pointer; }

  .button:hover {
      color: $color-special; }

  .button_header {
      border: solid 0.2em;
      margin-top: 1em;
      display: flex;
      .left {
        flex-grow: 5;
        display: flex;
        align-items: baseline;
        h2, h3 {
          display: inline-block;
          @media all and (min-width: 600px) {
            margin: 1em; }
          @media all and (max-width: 600px) {
            margin: 0.5em; } }
        h3 {
          margin-left: 0; } }
      .right {
        @media all and (min-width: 600px) {
          flex-grow: 5; }
        @media all and (max-width: 600px) {
          flex-grow: 0;
          display: none; }
        .span {
          //border: solid 1px
          letter-spacing: 0.1em;
          margin-right: 1em;
          position: absolute;
          top: 50%;
          right: 0;
          text-align: right;
          transform: translateY(-50%); } } }

  .button_class_broad {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: flex-start;

    .button_class {
      letter-spacing: 0.1em;
      border: solid 0.2em;
      @media all and (min-width: 600px) {
        width: 14%; }
      @media all and (max-width: 600px) {
        width: 34%; }

      margin: 1em;
      margin-left: 0em;
      span {
        @include center;
        text-align: center; } }

    .button_class:after {
      content: "";
      display: block;
      padding-bottom: 100%; } } }

